import React from 'react';
import styled from 'styled-components';

const allFeatures = require('./data/features.json') as FeatureMap;

type Feature = {
    name: string,
    desc: string,
    color: string
} & (
        { rect: [number, number, number, number] } |
        { cells: Array<[number, number]> } |
        { shape: Array<string> }
    );

type FeatureMap = { [key: string]: Array<Feature> };

type Props = {
    sourceRow?: number,
    sourceCol?: number,
    cellSize: number
};

const LabelTextShadow = styled.text`
    font-size: 13px;
    font-family: Raleway;
    font-style: italic;
    font-weight: 600;
    text-anchor: middle;
    text-align: center;
    dominant-baseline: middle;
    stroke-linejoin: bevel;
    stroke: black;
    stroke-width: 4px;
    &:hover {
        stroke: #888;
        stroke-width: 12px;
        stroke-opacity: 0.8;
    }
`;

const LabelText = styled.text`
    font-size: 13px;
    font-family: Raleway;
    font-style: italic;
    font-weight: 600;
    text-anchor: middle;
    text-align: center;
    dominant-baseline: middle;
    fill: white;
    pointer-events: none
`;

function mapLabel(x: number, y: number, name: string, fontSize: string = '12px') {
    return (
        <g>
            <LabelTextShadow x={x} y={y}>
                {name}
            </LabelTextShadow>
            <LabelText x={x} y={y}>
                {name}
            </LabelText>
        </g >);
}

const INSET = 24;

export default function RegionGrid(props: Props) {

    function rectangularFeature(feature: Feature & { rect: [number, number, number, number] }) {
        const [x1, y1, x2, y2] = feature.rect;
        const rectProps = {
            x: x1 * cellSize + INSET,
            y: y1 * cellSize + INSET,
            width: (x2 - x1) * cellSize,
            height: (y2 - y1) * cellSize
        }
        return <g>
            <rect
                {...rectProps}
                style={{
                    fill: feature.color
                }}
                opacity={0.4}
            />
            <rect
                {...rectProps}
                style={{
                    stroke: feature.color,
                    fill: 'transparent'
                }}
            />
            {mapLabel((x1 + x2) * cellSize / 2 + INSET, (y1 + y2) * cellSize / 2 + INSET, feature.name)}
        </g>
    }

    function shapedFeature(cells: Array<[number, number]>, color: string, name: string) {
        const [centerX, centerY] = cells.reduce(
            ([sumX, sumY], [x, y]) => [x / cells.length + sumX, y / cells.length + sumY],
            [0, 0]
        )
        return <g>
            {cells.map(
                ([x, y]) => (
                    <rect
                        x={x * cellSize + INSET} y={y * cellSize + INSET}
                        width={cellSize} height={cellSize}
                        style={{
                            fill: color,
                            opacity: 0.5,
                            stroke: color,
                            strokeOpacity: 1.0,
                        }}
                    />
                ))
            }
            {mapLabel(
                centerX * cellSize + cellSize / 2 + INSET,
                centerY * cellSize + cellSize / 2 + INSET,
                name)}
        </g>
    }

    const { sourceRow, sourceCol, cellSize } = props;

    if (sourceRow == null || sourceCol == null) {
        return <svg
            height={cellSize * 16 + INSET * 2}
            width={cellSize * 16 + INSET * 2}
            style={{
                margin: '10px',
                background: '#101020'
            }}
        >
            {mapLabel(8 * cellSize + cellSize / 2, 8 * cellSize, "Select a square", '24px')}

        </svg>
    }

    const featureKey = `${sourceRow}-${sourceCol}`;
    const features = allFeatures[featureKey];

    return (<svg
        height={cellSize * 16 + 2 * INSET}
        width={cellSize * 16 + 2 * INSET}
        style={{
            margin: '10px',
            background: '#101020'
        }}
    >
        <rect x={INSET} y={INSET} style={{ stroke: 'cyan' }} height={cellSize * 16} width={cellSize * 16} />
        {
            [...new Array(15)].map((_, n) => (
                <g>
                    <line
                        x1={INSET} y1={n * cellSize + cellSize + INSET}
                        x2={INSET + cellSize * 16} y2={n * cellSize + cellSize + INSET}
                        stroke='#555'
                    />
                    <line
                        y1={INSET} x1={n * cellSize + cellSize + INSET}
                        y2={INSET + cellSize * 16} x2={n * cellSize + cellSize + INSET}
                        stroke='#555'
                    />
                </g>
            ))
        }
        {
            (features != null && typeof features !== 'string') && features.map(
                (feature) => {
                    if ('rect' in feature) {
                        return rectangularFeature(feature)
                    } else if ('shape' in feature) {
                        const { shape } = feature;
                        const cells: Array<[number, number]> = [];
                        for (let i = 0; i <= 15; i++) {
                            for (let j = 0; j <= 15; j++) {
                                if (shape[i].charAt(j) !== '.') cells.push([j, i]);
                            }
                        }
                        return shapedFeature(cells, feature.color, feature.name);
                    } else {
                        return shapedFeature(feature.cells, feature.color, feature.name);
                    }
                }
            )
        }
    </svg>)
}