import React, { useState } from 'react';

import grid from './data/grid.json';
import RegionGrid from './RegionGrid';

// import as an "any" to get around some typechecking
const gridDefs = require('./data/gridDefs.json');

type Props = {
    rows: number,
    cols: number,
    tileSize: number
}

export default function MapGrid(props: Props) {
    const { rows, cols, tileSize } = props;

    const [focus, setFocus] = useState<[number, number] | null>(null);
    const [featuredTile, setFeaturedTile] = useState<[number, number] | null>(null);
    const focusChar = focus == null ? '.' : grid[focus[0]].charAt(focus[1]);

    const focusTile = gridDefs[focusChar];

    function makeRects(rows: number, cols: number, size: number, focusChar: string) {
        const out = [];
        for (let row = 0; row < rows; row++) {
            for (let col = 0; col < cols; col++) {
                const ch = grid[row][col];
                out.push(
                    <g>
                        <rect
                            x={col * size}
                            y={row * size}
                            width={size}
                            height={size}
                            onMouseOver={() => setFocus([row, col])}
                            onClick={() => setFeaturedTile([row, col])}
                            style={{
                                fill: gridDefs[ch].color,
                                stroke: 'rgba(0, 0, 0)',
                            }}
                        />
                        {focusChar === ch && focusChar !== '.' && focusChar !== '_' && < rect
                            x={col * size}
                            y={row * size}
                            width={size}
                            height={size}
                            style={{
                                fill: 'white',
                                pointerEvents: 'none'
                            }}
                            opacity={0.5}
                        />}
                    </g>
                )
            }
        }
        return out;
    }

    let rects = makeRects(rows, cols, tileSize, focusChar);

    return (

        <div style={{ flexShrink: 1, display: 'flex', flexDirection: 'column' }}>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
                <svg
                    height={rows * tileSize}
                    width={cols * tileSize}
                    style={
                        {
                            border: '1px solid white',
                            margin: '10px'
                        }
                    }
                    onMouseLeave={() => setFocus(null)}
                >
                    {rects}
                    {
                        featuredTile != null && <rect
                            x={featuredTile[1] * tileSize}
                            y={featuredTile[0] * tileSize}
                            width={tileSize}
                            height={tileSize}
                            stroke={'white'}
                            strokeWidth='3px'
                            fill='transparent'
                        />
                    }
                </svg>
                <RegionGrid sourceRow={featuredTile?.[0]} sourceCol={featuredTile?.[1]} cellSize={24} />
            </div>
            <div style={{ display: 'flex', flexDirection: 'column' }}>
                <div style={
                    {
                        fontSize: '18px',
                        fontFamily: 'Raleway',
                        fontWeight: 300,
                        color: 'white',
                        margin: '10px',
                        maxWidth: `${cols * tileSize}px`
                    }
                }>
                    {focus == null ? 'Hover over the map' : focusTile.name}
                </div>
                <div style={
                    {
                        fontSize: '14px',
                        fontFamily: 'Raleway',
                        fontWeight: 300,
                        color: 'white',
                        margin: '0px 10px 10px 10px',
                        maxWidth: `${25 * tileSize}px`
                    }
                }>
                    {focus == null ? ' ' : focusTile.description}
                </div>
            </div>
        </div>
    );
}